import '../../App.css';
import './CardsFCC.css';

import PortfolioLink from './PortfolioLink';
import RWD from '../../images/ResponsiveWebDesign.png';
import JS from '../../images/JavaScript.jpg';
import React from '../../images/React.jpg';

function CardsFMB() {
  return (
    <div className='cards'>
      <h1>Check out these EPIC Projects!</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <PortfolioLink
              src={RWD}
              text='Responsive Web Design'
              label='Certification'
              path='https://joeluciano.github.io/website/FrontEndMastersCourse/Star-Mole/'
            />
            <PortfolioLink
              src={JS}
              text='JavaScript Algorithms and Data Structures'
              label='Certification'
              path='https://joeluciano.github.io/website/FrontEndMastersCourse/5-github/begin/calculator.html'
            />
            <PortfolioLink
              src={React}
              text='Front End Development Libraries'
              label='Certification'
              path='https://joeluciano.github.io/website/FrontEndMastersCourse/5-github/begin/calculator.html'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default CardsFMB;
